import React, { useEffect, useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import 'bootstrap/dist/css/bootstrap.min.css';
import queryString from 'query-string';
import axios from 'axios'
import img from '../images/cropped-logo2.png'
import Navbars from './Navbar';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import Loading from './loading';
import { Navigate, useNavigate } from 'react-router-dom';


const baseUrl = 'https://masala90.herokuapp.com/api/user'



export default function ResetPassword() {
  /* eslint-disable no-restricted-globals */
  const navigate = useNavigate()

  const [invalidUser, setInvalidUser] = useState()
  const [newPassword, setNewPassword] = useState()
  const [password, setPassword] = useState()
  const [fail, setFail] = useState(false)
  const [fails, setFails] = useState(false)
  const [success, setSuccess] = useState(false)
  const { token, id } = queryString.parse(location.search)
  const [loading, setLoading] = useState(false)



  const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });



  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }


    setFail(false)
    setFails(false)
    setSuccess(false)
  };


  const verifyToken = async () => {

    try {



      const { data } = await axios(`${baseUrl}/verify-token?token=${token}&id=${id}`)





    } catch (error) {
      if (error?.response?.data) {
        const { data } = error.response
        if (!data.success) return setInvalidUser(data.error)

        console.log(error.response.data)

      }
      console.log(error)

    }

  }

  useEffect(() => {
    verifyToken()
  }, [])


  const handleOnChange = (e) => {
    console.log(e.target.value)


    setPassword(e.target.value)
  }
  const handleOnChange1 = (e) => {
    console.log(e.target.value)


    setNewPassword(e.target.value)
  }



  const handleOnSubmit = async (e) => {
    console.log("ello")
    e.preventDefault()

    const passwords = password
    const newPasswords = newPassword
    console.log(passwords.trim().length, passwords !== newPasswords)
    if (+passwords.trim().length < 8 || +passwords.trim().length > 20) { return setFail(true) }



    if (passwords !== newPasswords) { return setFails(true) }




    try {
      setLoading(true)

      const { data } = await axios.post(`${baseUrl}/reset-password?token=${token}&id=${id}`, { password: passwords })

      if (data.success) {
        setLoading(false)

        setSuccess(true)
        navigate('/login')

      }
    } catch (error) {
      if (error?.response?.data) {
        const { data } = error.response
        if (!data.success) return setInvalidUser(data.error)

        console.log(error.response.data)
        setLoading(false)

      }
    }



  }





  return (<>
    <>
      {loading ? <Loading /> : <>

        {!invalidUser ?

          <> <div style={{ justifyContent: "center", display: "flex", alignItems: "center", alignContent: "center", margin: "10px" }}>   <img src={img} alt={'image'} ></img></div>

            <div style={{ margin: "40px" }}>
              <Snackbar open={fail} autoHideDuration={2000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                  Password should be greater than 8 and smaller than 20 char!
                </Alert>
              </Snackbar>
              <Snackbar open={fails} autoHideDuration={2000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                  Password does not match
                </Alert>
              </Snackbar>
              <Snackbar open={success} autoHideDuration={2000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                  Your password changed succesfully!
                </Alert>
              </Snackbar>

              <Form >




                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label>Password</Form.Label>
                  <Form.Control type="password" placeholder="Password" onChange={(e) => handleOnChange1(e)} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label> Confirm Password</Form.Label>
                  <Form.Control type="password" placeholder="Password" onChange={(e) => handleOnChange(e)} />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicCheckbox">
                  <Form.Check type="checkbox" label="Check me out" />
                </Form.Group>
                <Button variant="primary" disabled={password && newPassword ? false : true} type="submit" onClick={handleOnSubmit}>
                  Submit
                </Button>
              </Form>



            </div></> : <><h1 style={{ justifyContent: "center", display: "flex", alignItems: "center", alignContent: "center", margin: "10px" }}>Invalid user</h1></>}
      </>} </>
  </>
  )
}

