import React, { useState } from "react";

import OtpInput from "react-otp-input";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';

import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import { makeStyles } from "@material-ui/core";
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import Loading from "./loading";


const useStyles = makeStyles(theme => ({
 grid: {
  backgroundColor: "grey",
  height: "50vh",
  textAlign: "center"
 },
 avatar: {
  margin: theme.spacing(1),
  backgroundColor: theme.palette.secondary.main
 },
 submit: {
  margin: theme.spacing(3, 0, 2)
 },
 paper: {
  marginTop: theme.spacing(8),
  display: "flex",
  flexDirection: "column",
  alignItems: "center"
 }
}));
const baseUrl = "https://masala90.herokuapp.com/api/user"

export default function Verification() {

 const location = useLocation();
 const navigate = useNavigate()
 const classes = useStyles();
 const [otp, setOtp] = useState()
 const [success, setSuccess] = useState(false)

 const [load, setLoad] = useState(false)

 const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
 });



 const handleClose = (event, reason) => {
  if (reason === 'clickaway') {
   return;
  }



  setSuccess(false)
 };

 const handleChange = (otp) => {

  setOtp(otp)
 };
 const onSubmit = async (e) => {
  e.preventDefault()
  setLoad(true)

  try {
   const { data } = await axios.post(`${baseUrl}/verify-email`, { userId: location.state.data, otp: otp })
   if (data.success) {

    setSuccess(true)
    setLoad(false)
    return navigate('/login')






    // navigation.dispatch(StackActions.replace('Verification'))

   }
  } catch (error) {
   if (error?.response?.data) {
    const { data } = error.response
    if (!data.success) return

    console.log(error.response.data)

   }
  }
 }
 return (
  <> <Snackbar open={success} autoHideDuration={2000} onClose={handleClose}>
   <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
    Your account verified succesfully!
   </Alert>
  </Snackbar>
   <Container component="main" maxWidth="sm">
    <CssBaseline />
    <div className={classes.paper}>
     {load ? <Loading /> : <> <Grid
      container
      style={{ backgroundColor: "white" }}
      className={classes.grid}
      justify="center"
      alignItems="center"
      spacing={3}
     >
      <Grid item container justify="center">
       <Grid item container alignItems="center" direction="column">
        <Grid item>
         <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
         </Avatar>
        </Grid>
        <Grid item>
         <Typography component="h1" variant="h5">
          Verification Code
         </Typography>
        </Grid>
       </Grid>
      </Grid>
      <Grid item xs={12} textAlign="center">
       <Paper elevation={0}>
        <Typography variant="h6">
         Please enter the verification code sent to your Email
        </Typography>
       </Paper>
      </Grid>
      <Grid
       item
       xs={12}
       container
       justify="center"
       alignItems="center"
       direction="column"
      >
       <Grid item spacing={3} justify="center">
        <OtpInput
         value={otp}
         onChange={handleChange}
         numInputs={4}

         inputStyle={{
          width: '50px',
          margin: "10px",
          height: '50px',
          borderColor: 'yellow'
         }}
        />
       </Grid>
       <Grid item>
        <Button
         type="submit"
         fullWidth
         variant="contained"
         color="primary"
         className={classes.submit}
         onClick={(e) => { onSubmit(e) }}
         disabled={load ? true : false}
        >
         Verify
        </Button>
       </Grid>
      </Grid>
     </Grid></>}
    </div>
   </Container>
  </>
 );
}
