import React, { useState, useEffect } from 'react'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import axios from 'axios'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent'
import img from '../images/img.jpg'
import logo from '../images/logo1.png'
import Bottom from './bottom';
import Paper from '@mui/material/Paper';
import Loading from './loading';
import moment from 'moment';

const baseUrl = "https://masala90.herokuapp.com/api/user"



function Orders() {


 const [data, setData] = useState([])

 const [searchdata, setsearchDatas] = useState('')













 const storeToken = async () => {






  await axios.get(`${baseUrl}/getorders`).then(function (response) {
   response.data.orders.orders.sort((a, b) => Date.parse(b.time) - Date.parse(a.time))
   setData(response.data.orders.orders)
  })















 }

 useEffect(() => {
  storeToken()
 }, [])





 const onchanged = (e) => {
  const event = e.target.value.toLowerCase()


  const searched = data.filter((d) => d.name.trim().toLowerCase().includes(event) || d.addBy?.trim().toLowerCase().includes(event) || d.address?.trim().toLowerCase().includes(event))

  setsearchDatas(searched)
 }
 return (
  <>
   {/* <div style={{ zIndex: '-1', position: "fixed", width: '30%', margin: "0px", hieght: "30%", marginTop: "-200px" }}>
    <img src={img} alt={"yellow-background"} />
   </div> */}


   <Paper sx={{ position: 'fixed', top: 0, left: 0, right: 0 }} elevation={0}>

    <img src={logo} alt="Logo" style={{
     display: 'flex',
     marginLeft: "auto",
     marginRight: "auto",
     width: "150px",



    }}></img>
    <Typography component="h1" variant="h5" sx={{ justifyContent: 'center', display: 'flex', alignItems: 'center', margin: '10px' }}>
     Orders
    </Typography>

    <TextField id="outlined-basic" label="search" variant="outlined" onChange={(e) => onchanged(e)} sx={{
     justifyContent: 'center', display: 'flex', marginLeft: '30px', marginRight: '30px'
    }} />


   </Paper>
   <Box sx={{
    width: '100%', background: { img }, height: 'auto', overflow: 'hidden',
    marginBottom: '40px', marginTop: '200px'
   }} >
    {data ? <> <Grid container spacing={2} alignItems="center" sx={{ padding: '50px' }} justify="center">

     {searchdata ? searchdata.map((x) => {

      return <Grid item xs={12} sm={6} md={4} alignItems="center" sx={{ minWidth: 225 }} ><Card sx={{ minHeight: 200 }} variant="outlined" >
       <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Name</b>: {x.name}
        </Typography>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Mobile </b>:{x.mobile}
        </Typography>

        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Store</b>: {x.storename}
        </Typography>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>

         <b>Salesman Name</b>: {x.addBy}
        </Typography>

        <Typography variant="body2">
         <b>Landmark</b>: {
          x.landmark
         }
        </Typography>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Time</b> : {x.time}
        </Typography>
        <Typography variant="body2">
         <b>Addresss</b>: {
          x.address
         }
        </Typography>
        <Typography variant="body2">
         <b>Order</b>: {
          x.order
         }
        </Typography>
       </CardContent>
       <CardActions>
        {/* <Button size="small" onClick={() => handleOpen(x)}>Take Orders</Button> */}
       </CardActions>
      </Card></Grid>

     }) : data.map((x) => {

      return <Grid item xs={12} sm={6} md={4} sx={{ minWidth: 225 }}> <Card sx={{ minHeight: 200 }} variant="outlined" key={x._id}>
       <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Name</b>: {x.name}
        </Typography>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Mobile </b>:{x.mobile}
        </Typography>

        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Store</b>: {x.storename}
        </Typography>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>

        </Typography>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>

         <b>Salesman Name</b>: {x.addBy}
        </Typography>
        <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
         <b>Time</b> : {moment(x.time).format(' h:mm:ss a YYYY-MM-DD ')}
        </Typography>
        <Typography variant="body2">
         <b>Landmark</b>: {
          x.landmark
         }
        </Typography>
        <Typography variant="body2">
         <b>Addresss</b>: {
          x.address
         }
        </Typography>
        <Typography variant="body2">
         <b>Order</b>: {
          x.order
         }
        </Typography>
       </CardContent>
       <CardActions>
        {/* <Button size="small" onClick={() => handleOpen(x)}>Take Orders</Button> */}
       </CardActions>
      </Card> </Grid>

     })}


    </Grid></> : <Loading />}
   </Box>
   <Bottom />


  </>

 )

}

export default Orders